export default{
	name:'tree-list',
	props:{
		typeList:{
			type:Array,
			default:function(){
				return []
			}
		},
		subTypeList:{
			type:Array,
			default:function(){
				return []
			}
		}
	},
	data() {
		return {
			height: 0,
			typeActive: 0,
			scrollTop: 0,
			scrollHeight: 0,
		}
	},
	methods: {
		scroll(e) {
			this.scrollHeight = e.detail.scrollHeight;
		},
		typeClickMain(categroy, index) {
			this.typeActive = index;
	
		},
		goProduct(id){
			uni.navigateTo({
				url:`/pages/goods/detail/detail`
			})
		}
	},
	created: function() {
		this.height = uni.getSystemInfoSync().windowHeight;
	}
}
